@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300..800;1,300..800&display=swap");

html {
  overflow-x: hidden;
}

* {
  margin: 0px;
  padding: 0px;
  border: 0px;
  font-family: Montserrat;
  scroll-behavior: smooth;
}

.textCenter {
  text-align: center;
  position: relative;
}
.textCenter a {
  margin-bottom: 4rem;
}

.bgWelcome {
  background-image: url("./img/bgWelcome.jpg");
  background-position: center;
  background-size: cover;
  height: 100dvh;
}
.bgWelcomeOverlay {
  background-color: rgb(0, 0, 0, 0.5);
  height: 100dvh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
}
.welcomeContentWrapper {
  display: flex;
  justify-content: center;
  flex-grow: 1;
}

.welcomeContentAlign {
  display: flex;
  flex-direction: column;

  gap: 2.5rem;

  justify-content: center;
  align-self: center;

  text-align: center;
  color: white;
  line-height: 1.6;

  padding: 0rem 1rem;
}
.welcomeContentWrapper h1 {
  font-size: 1.7rem;
  line-height: 38px;
}

.welcomeTextOne {
  text-transform: uppercase;
  color: #ddd;
  font-weight: 600;
  font-size: 0.75rem;
  letter-spacing: 2px;
}
.welcomeTextTwo {
  font-size: 1rem;
  font-family: Open Sans;
}

.bgQuem {
  padding: 4rem 1.2rem;
  display: flex;
  flex-direction: column;
  position: relative;
}

.bgQuem .decor {
  align-self: flex-start;
  margin-top: 0.5rem;
  margin-bottom: 2rem;
  margin-left: 2px;
}

.title {
  font-weight: 600;
  line-height: 2rem;
  font-size: 1.5rem;
  color: #21242d;
}

.servicesWrapper .title {
  margin-bottom: 15px;
}

.titleDesc {
  text-transform: uppercase;
  font-size: 0.7rem;
  font-weight: 600;
  letter-spacing: 1px;
  color: #888;

  margin-bottom: 15px;
}

.firstL {
  float: left;
  font-size: 3.25rem;
  font-weight: 600;
  margin-top: 10px;
  margin-right: 8px;
  line-height: 0.5;
  color: #2f3148;
}

.quemContentWrapper {
  margin-top: 8dvh;
}
.quemContentWrapper p {
  text-align: left;
  font-size: 0.9rem;
  line-height: 1.5rem;
  color: #4d5464;

  font-family: Open Sans;
}

.quemContentWrapper ul {
  list-style: none;
  margin-top: 7dvh;
  display: flex;
  flex-direction: column;
  gap: 5dvh;
}

.quemContentWrapper ul li {
  font-weight: 600;
  font-size: 0.75rem;
  text-transform: uppercase;

  color: #21242d;

  display: flex;
  align-items: center;
  gap: 10px;
}

.quemContentWrapper ul li::before {
  content: url("./img/checkmark-icon.svg");
  width: 2rem;
  height: 2rem;
}

.colorStripe {
  background-color: #2f3148;
  color: #fff;

  font-weight: 600;
  font-size: 0.75rem;

  display: flex;
  flex-direction: column;
  justify-content: center;

  gap: 1.5rem;

  padding: 20px 16px;

  text-align: center;
}

.colorStripe a {
  font-size: 0.75rem;
}

.servicesAlign {
  padding: 4rem 0rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.servicesAlign .decor {
  margin-top: 1rem;
}
.servicesWrapper {
  padding: 4rem 1rem;
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  column-gap: 4dvw;
  row-gap: 5rem;
  justify-content: center;
}

.servicesWrapper p {
  font-size: 0.875rem;
  line-height: 24px;
  color: #4d5464;
  font-family: Open Sans;
}
.pqNos {
  display: flex;
  flex-direction: column;
  background-color: #2f3148;
  padding: 4rem 1rem;
}

.pqNos .nosContentContainer {
  color: #fff;

  padding: 0rem 1rem;

  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.pqNos .nosImgContainer img {
  width: 100%;
  margin-bottom: 2.5rem;
  border-radius: 5px;
}

.pqNosText {
  font-size: 0.875rem;
  font-family: Open sans;
  line-height: 1.5rem;
}
.pqNos .titleDesc {
  color: #ffffffb2;
}
.pqNos .title {
  color: #ffffff;
}

.atendWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8dvh;
  margin-top: 5dvh;
  margin-bottom: 3dvh;
}

.atendWrapper h3 {
  font-size: 1rem;
  margin-bottom: 0.5rem;
  font-weight: 600;
  position: relative;
}

.atendWrapper h3::before {
  content: url("./img/checkmark-icon-white.svg");
  position: absolute;
  top: 50%;
  left: -17%;
  transform: translateY(-50%);
}
.atendWrapper div p {
  font-family: open sans;
  font-size: 0.75rem;

  line-height: 1.5;
}
.atendWrapper a {
  margin-top: 1rem;
}

.atendItem {
  width: 75%;
}
.contWrapper {
  position: relative;

  display: flex;
  flex-direction: column;
  gap: 5dvh;
  padding: 4rem 1rem;
  text-align: center;
  align-items: center;
}
.contItemsWrapper {
  display: flex;
  flex-direction: column;
  gap: 12dvh;
}

footer {
  background-color: #2f3148;
  padding: 0rem 1rem;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  border-top: 1px solid rgb(255, 255, 255, 1);
}

.footerLogoWrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;

  align-items: center;
  padding: 2.5rem 0rem;
}
.footerLogoWrapper img {
  width: 40%;
}
.footerLogoWrapper div p {
  display: none;
}
.footerDesc {
  width: 100dvw;
  border-top: 1px solid rgba(255, 255, 255, 0.3);

  overflow-x: hidden;

  padding: 1rem 0rem;
}

.footerDesc p {
  color: #ffffff99;
  font-size: 0.75rem;
  font-family: open sans;
  margin-left: 10px;
  padding-left: 1rem;
}

.decor {
  display: flex;
  justify-content: center;
}

.decorWhite {
  background-color: #fff;
  width: 20px;
  height: 2px;
}

.decorBlack {
  background-color: #21242d;
  width: 20px;
  height: 2px;
}

/* MEDIA QUERIES */

@media (min-width: 426px) {
  .title {
    font-size: 2rem;
  }

  .bgQuem {
    overflow-x: hidden;
  }
  .quemContentWrapper {
    margin-top: 2rem;
  }
  .quemContentWrapper ul {
    margin-top: 2rem;

    display: grid;
    grid-template-columns: 50% 50%;
    column-gap: 2vw;
  }
  .atendWrapper {
    margin-top: 2rem;
    margin-bottom: 3rem;
    gap: 2rem;
  }
  .atendWrapper h3 {
    font-size: 1.125rem;
  }
  .atendWrapper div p {
    font-size: 0.875rem;
  }

  .atendWrapper h3:before {
    left: -50px;
  }

  .pqNos .nosImgContainer img {
    max-height: 500px;
    object-fit: cover;
    object-position: top;
  }

  .pqNosText {
    font-size: 1rem;
  }

  .footerLogoWrapper {
    row-gap: 1.4rem;
  }

  .footerLogoWrapper div:nth-of-type(1) {
    display: flex;
    gap: 1rem;
    color: white;
  }
  .footerLogoWrapper div p {
    display: inline-block;
    font-size: 0.8rem;
  }
}

@media (min-width: 768px) {
  .contWrapper {
    overflow-x: hidden;
  }
  .welcomeContentWrapper h1 {
    font-size: 2.125rem;
  }

  .contItemsWrapper {
    display: grid;
    grid-template-columns: 40dvw 40dvw;
    justify-content: center;
    justify-items: stretch;
    column-gap: 0rem;
    row-gap: 3rem;
  }

  .servicesWrapper p {
    font-size: 1rem;
  }
}

@media (min-width: 1024px) {
  .welcomeContentWrapper .welcomeContentAlign {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 2rem;
    max-width: 70dvw;
    padding-bottom: 3.5rem;
  }
  .bgQuem {
    display: grid;
    grid-template-columns: 45dvw 45dvw;
    justify-content: center;
    gap: 1.4rem;

    padding: 6rem 0rem;
  }
  .bgQuem .decor {
    display: none;
  }
  .quemContentWrapper p {

    font-size: 1rem;

  }
  .servicesWrapper {

    column-gap: 1rem;

  }
  .servicesAlign {
    padding-bottom: 0px;
  }
  .pqNos {
    display: grid;
    grid-template-columns: 45dvw 45dvw;
    justify-content: center;
    position: relative;
    gap: 2rem;
    align-items: center;
  }
  .pqNos a {
    align-self: flex-start;
    margin-left: 4.5rem;
  }
}
/* ANCHOR POINTS */

.anchorEstilo {
  visibility: hidden;
  position: absolute;
  top: 0;
}
