.cardWrapper {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  max-width: 360px;
}
.cardImgWrapper {
  position: relative;
  width: 100%;

  height: 0;
  padding-top: calc(100% * (200 / 360));

  margin-bottom: 2rem;
}

.cardImg {
  position: absolute;
  top: 0%;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 5px;
}

.cardIconWrapper {
  background-color: #2f3148;
  align-self: center;
  padding: 10px;
  border-radius: 50%;
  display: flex;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 50%);
}

.cardWrapper h3 {
  font-size: 1rem;
  color: #21242d;
  font-weight: 600;
}

section a {
  text-align: center;
}

.decor {
  display: flex;
  justify-content: center;
}

.decorWhite {
  background-color: #fff;
  width: 20px;
  height: 2px;
}

.decorBlack {
  background-color: #21242d;
  width: 20px;
  height: 2px;
}

@media (min-width: 769px) {
  .cardWrapper {
    max-width: 400px;
  }

  .cardWrapper h3 {
    font-size: 1.125rem;
  }
}
