.contItemsWrapper {
  display: flex;
  text-align: left;
  gap: 1.2rem;
  align-items: center;
}

.contIconWrapper {
  width: 2rem;
  height: min-content;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 15%;
  padding: 10px 10px 7px 10px;
  flex-shrink: 0;
}
.contIconWrapper img {
  width: 100%;
}

.contDescText {
  font-size: 1rem;
  color: #21242d;
  font-weight: 600;
}

.contDescLink {
  text-align: left;
  text-decoration: underline;
  color: #4d5464;
  font-size: 0.75rem;
  font-family: Open Sans;
  font-weight: 700;
}

@media (min-width: 767px) {
  .contItemsWrapper {
    display: grid;
    grid-template-columns: auto 50%;
    justify-content: center;
    align-items: center;

    column-gap: 1rem;
  }

  .contIconWrapper {
    grid-column: 1/2;
  }
}
