nav {
  padding: 5px 0px;
  display: flex;
  justify-content: space-between;
  align-items: center;

  border-bottom: 1px solid rgb(255, 255, 255, 0.2);
}
header ul {
  list-style: none;
}
header ul li {
  padding: 1rem 1.3rem;

  font-size: 0.8rem;
  color: white;
  background-color: #21242d;

  border-bottom: 1px solid rgba(161, 156, 156, 0.2);
}
header ul li a {
  color: white;
  text-decoration: none;
}
.logoHeader {
  width: 8.5rem;
  display: flex;
  align-items: center;
  margin: 0rem 1rem 0rem 1rem;
}

.logoHeader img {
  width: 100%;
  height: 100%;
}

.menuMob {
  padding: 8px 10px;

  display: flex;
  flex-direction: column;
  gap: 5px;
  width: fit-content;
  height: 25px;
  border-left: 1px solid rgba(255, 255, 255, 0.2);
  justify-content: center;
}

.menuMob span {
  display: block;
  position: relative;
  width: 25px;
  height: 3px;
  background-color: white;
  border-radius: 5px;
  transition: all 0.5s ease-in-out, opacity 0.3s ease 0.3s;
}

.menuMob span:nth-of-type(1) {
  transform: rotate(0deg);
  top: 0%;
}

.menuMob span:nth-of-type(2) {
  opacity: 1;
}

.menuMob span:nth-of-type(3) {
  transform: rotate(0deg);
  top: 0%;
}

.menuMobOpen {
  padding: 8px 10px;
  display: flex;
  flex-direction: column;
  gap: 5px;
  width: fit-content;
  height: 25px;
  border-left: 1px solid rgb(255, 255, 255, 0.2);
  justify-content: center;
  align-items: center;
}
.menuMobOpen span {
  display: block;
  width: 25px;
  height: 3px;
  border-radius: 5px;
  background-color: white;
  position: relative;
  transition: all 0.5s ease-in-out, opacity 0.3s;
}
.menuMobOpen span:nth-of-type(1) {
  transform: rotate(45deg);
  top: 32%;
}

.menuMobOpen span:nth-of-type(2) {
  opacity: 0;
}

.menuMobOpen span:nth-of-type(3) {
  transform: rotate(-45deg);
  top: -32%;
}

.hidden {
  width: 100dvw;
  position: absolute;
  overflow: hidden;

  transition: height 0.4s ease-in-out;
}

.show {
  width: 100dvw;
  position: absolute;
  overflow: hidden;

  transition: height 0.4s ease-in-out;
}
.menuDesk {
  display: none;
}
/* MEDIA QUERIES */

@media (min-width: 768px) {
  .menuMob {
    display: none;
  }

  .menuDesk {
    display: flex;
  }
  .menuDesk li {
    background-color: transparent;
    font-weight: 600;
    border: 0px;
  }
  nav {
    padding: 5px 2.5rem;
  }
}
